import {
	api,
	barId
} from '../../const'
import card from '../../components/card'
import bottomAction from '../../components/bottomAction'
import {
	checkT
} from '../../utils/net'
import login from '../../components/login'



export default {
	data() {
		return {
			currentData: [],
			t: 0,
			bottomActionVisible: false,
			deletedPostId: 0,
			type: '',
			otherUserId: 0,
			loginVisible: false
		}
	},
	components: {
		card,
		bottomAction,
		login
	},
	onLoad(options) {
		this.type = options.type
		console.log('type=' + this.type)
		this.t = uni.getStorageSync('t')
		this.otherUserId = options.otherUserId
		this.loadData()

	},
	onShow() {
		var that = this;
		var t = uni.getStorageSync("t")
		checkT(t,
			function() {
				that.loginVisible = true
			},
			function() {}
		);
	},
	methods: {
		async loadData() {
			this.$http({
				act: 'collection.post.list',
				t: this.t,
				pageNum: 0,
				size: 10
			}, function(res) {
				this.currentData = res.data.list.map(_ => {
					return _.post;
				})
			})
		},
		// closeModal: function () {
		//   this.bottomActionVisible = false
		// },
		// showActionModal: function (data) {
		//   this.bottomActionVisible = true
		//   this.deletedPostId = data
		//
		// },
		reloadCardList() {
			this.loadData()
		}
	}
}
